import React, { useCallback, useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate, useLocation  } from 'react-router-dom';
import { 
    Form,
    FormGroup,
    Label,
    Input,
    FormText,
    Button

} from 'reactstrap';
import { curMenuHandler } from '../../../redux/menu/action';
import { bypassCheckHandler, loginHandler } from '../../../redux/user/action';

const Login = () => {
    const [id, setId] = useState('');
    const [password, setPassword] = useState('');
    const [bypass, setBypass] = useState(false);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const loc = useLocation();
    console.log("",useLocation());

     const loginInfo = useSelector(state => state.userReducer.loginInfo);
     const loginChkInfo = useSelector(state => state.userReducer.loginChkInfo);

     useEffect(() => {
        if(loginInfo.id != null && loginInfo.id !== '') {
          setId(loginInfo.id);
        }
        
        dispatch(curMenuHandler({no:1, name:'', link: loc.pathname}));
     },[]);

     useEffect(() => {
        console.log(" useEffect:",loginChkInfo);
        if(!loginChkInfo.status && loginChkInfo.msg !== '') {
          alert(loginChkInfo.msg);
        } else if(loginChkInfo && loginChkInfo.msg !== '') {
          alert(loginChkInfo.msg);
          navigate("/main/home");
        } 
   },[loginChkInfo]);


     const onChangeId = (e) => {
         setId(e.target.value);
     }
     const onChangePassword = (e) => {
         setPassword(e.target.value);
     }
     const onClickCheckbox = (e) => {
         setBypass(e.target.checked);
     }
     const onSubmit = (e) => {
         e.preventDefault();
         if(id === '') return alert(' .');
         if(password === '') return alert(' .');
         const obj = {id: id, password: password, bypass: bypass};
         console.log("obj:",obj);
         dispatch(loginHandler(obj));
      
     }
     const onClickSignup = () => {
         console.log("")
         navigate("/user/signup");
     }

    return ( 
        <Form onSubmit={onSubmit}>
        <FormGroup>
          <Label for="userId">
            Id
          </Label>
          <Input
            id="userId"
            name="id"
            placeholder=" ."
            type="text"
            value={id}
            onChange={onChangeId}
          />
        </FormGroup>
        <FormGroup>
          <Label for="userPassword">
            Password
          </Label>
          <Input
            id="userPassword"
            name="password"
            placeholder=" ."
            type="password"
            value={password}
            onChange={onChangePassword}
          />
        </FormGroup>
        <FormGroup check>
          <Input type="checkbox" onClick={(e)=> onClickCheckbox(e)}/>
          {' '}
          <Label check>
            
          </Label>
        </FormGroup>
        <Button type="submit">
          
        </Button>
        <Button onClick={()=> onClickSignup()}>
          
        </Button>
    </Form>
    );
};

export default Login;