import React, { useCallback, useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate, useLocation } from 'react-router-dom';
import { 
    Form,
    FormGroup,
    Label,
    Input,
    FormText,
    Button

} from 'reactstrap';
import { curMenuHandler } from '../../../redux/menu/action';
import { signupHandler } from '../../../redux/user/action';

const Signup = () => {
    const [id, setId] = useState('');
    const [password, setPassword] = useState('');
    const [name, setName] = useState('');
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const loc = useLocation();

    useEffect(() => {
        dispatch(curMenuHandler({no:2, name:'', link: loc.pathname}));
    },[])

    const onChangeId = (e) => {
        setId(e.target.value);
    }
    const onChangePassword = (e) => {
        setPassword(e.target.value);
    }
    const onChangeName = (e) => {
        setName(e.target.value);
    }
    const onSubmit = (e) => {
        e.preventDefault();
        if(id === '') return alert(' .');
        if(password === '') return alert(' .');
        if(name === '') return alert(' .');
        const obj = {bypass:false, id: id, password: password, name: name};
        dispatch(signupHandler(obj));
        alert(' .');
        navigate('/user/login');
    }

    return (
        <Form onSubmit={onSubmit}>
        <FormGroup>
          <Label for="userId">
            Id
          </Label>
          <Input
            id="userId"
            name="id"
            placeholder=" ."
            type="text"
            value={id}
            onChange={onChangeId}
          />
        </FormGroup>
        <FormGroup>
          <Label for="userPassword">
            Password
          </Label>
          <Input
            id="userPassword"
            name="password"
            placeholder=" ."
            type="password"
            value={password}
            onChange={onChangePassword}
          />
        </FormGroup>
        <FormGroup>
          <Label for="userName">
            
          </Label>
          <Input
            id="userName"
            name="name"
            placeholder=" ."
            type="text"
            value={name}
            onChange={onChangeName}
          />
        </FormGroup>
        <Button type="submit">
          
        </Button>
    </Form>
    );
};

export default Signup;