import { CUR_MENU } from "./actionType"

const initialState = {
    curMenu: {no: 0, name:'', link: ''},
    menuList: [
              {no: 1, name: '', link: '/user/login'},
              {no: 2, name: '', link: '/user/signup'},
              {no: 3, name: '', link: '/main/home'},
              {no: 4, name: ' ', link: '/ticket/list'},
              {no: 5, name: ' ', link: '/ticket/confirm'}
            ]
}

const menuReducer = (state=initialState, action) => {
    switch(action.type) {
        case CUR_MENU:
            return {
                ...state,
                curMenu : action.payload
            }
        default: return state;
    }
}

export default menuReducer;